import React, { createContext, useContext, useState, ReactNode } from "react";

interface FocusStackContextType {
  focusedWindow: string | null;
  focusStack: string[];
  setFocusStack: (stack: string[]) => void;
  focusWindow: (windowId: string) => void;
  removeFromFocusStack: (windowId: string) => void;
  isFocused: (windowId: string) => boolean;
  getFocusIndex: (windowId: string) => number;
}

const FocusStackContext = createContext<FocusStackContextType | undefined>(
  undefined
);

interface FocusStackProviderProps {
  children: ReactNode;
}

export const FocusStackProvider: React.FC<FocusStackProviderProps> = ({
  children,
}) => {
  const [focusStack, setFocusStack] = useState<string[]>([]);

  const focusWindow = (windowId: string) => {
    setFocusStack((prev) => {
      const newStack = [...prev];
      const existingIndex = newStack.findIndex((id) => id === windowId);

      if (existingIndex !== -1) {
        newStack.splice(existingIndex, 1);
      }
      newStack.push(windowId);
      return newStack;
    });
  };

  const removeFromFocusStack = (windowId: string) => {
    setFocusStack((prev) => {
      const newStack = [...prev];
      const index = newStack.findIndex((id) => id === windowId);
      if (index !== -1) {
        newStack.splice(index, 1);
      }
      return newStack;
    });
  };

  const isFocused = (windowId: string) => {
    return focusStack.at(-1) === windowId;
  };

  const getFocusIndex = (windowId: string) => {
    return focusStack.findIndex((id) => id === windowId);
  };

  const focusedWindow = focusStack.at(-1) ?? null;

  const value: FocusStackContextType = {
    focusedWindow,
    focusStack,
    setFocusStack,
    focusWindow,
    removeFromFocusStack,
    isFocused,
    getFocusIndex,
  };

  return (
    <FocusStackContext.Provider value={value}>
      {children}
    </FocusStackContext.Provider>
  );
};

export const useFocusStack = () => {
  const context = useContext(FocusStackContext);
  if (context === undefined) {
    throw new Error("useFocusStack must be used within a FocusStackProvider");
  }
  return context;
};
