import classNames from "classnames";

interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  children: React.ReactNode;
  className?: string;
}

export const Button = ({
  children,
  onClick,
  className,
  ...props
}: ButtonProps) => {
  return (
    <div className="border border-slate-500">
      <button
        className={classNames(
          "flex items-center p-1 emboss-out text-black hover:bg-slate-400 active:emboss-in",
          className
        )}
        onClick={onClick}
        {...props}
      >
        {children}
      </button>
    </div>
  );
};
