import React from "react";
import Dragger from "./Dragger";
import Resizer from "./Resizer";
import classNames from "classnames";
import { Applet } from "./Desktop";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faClose, faMinus } from "@fortawesome/free-solid-svg-icons";
import { faSquare, faSquareMinus } from "@fortawesome/free-regular-svg-icons";

import "animate.css";
import wait from "waait";
import { Button } from "./Button";
import { useFocusStack } from "../providers/FocusStackProvider";

interface Props {
  applet: Applet;
  onResize: (
    element: HTMLElement,
    direction: string,
    e: MouseEvent,
    applet: Applet
  ) => void;
  onDrag: (element: HTMLElement, e: MouseEvent, applet: Applet) => void;
  onClose: (applet: Applet) => void;
  onMinimise: (applet: Applet) => void;
  onMaximise: (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>,
    element: HTMLElement,
    applet: Applet
  ) => void;
}

export const Window = ({
  applet,
  onResize,
  onDrag,
  onClose,
  onMinimise,
  onMaximise,
}: Props) => {
  const ref = React.useRef<HTMLDivElement>(null);
  const { focusWindow, removeFromFocusStack, isFocused, getFocusIndex } = useFocusStack();

  const isWindowFocused = isFocused(applet.id);
  const focusIndex = getFocusIndex(applet.id);


  React.useEffect(() => {
    if (!ref.current) return;
    const windowRef = ref.current;
    if (applet.fullscreen) {
      windowRef.style.top = "0";
      windowRef.style.left = "0";
      windowRef.style.transform = "none";
      windowRef.style.width = "100%";
      windowRef.style.height = "calc(100% - 32px)";
    } else {
      const { height, width, left, top, right, bottom } = applet.floatingPosition;
      windowRef.style.height = height;
      windowRef.style.width = width;
      left && (windowRef.style.left = left);
      top && (windowRef.style.top = top);
      right && (windowRef.style.right = right);
      bottom && (windowRef.style.bottom = bottom);
    }
    focusWindow(applet.id);

    return () => {
      removeFromFocusStack(applet.id);
    };
  }, [applet]);

  const closeWindow = async () => {
    ref.current?.classList.add("animate__fadeOut");
    await wait(260);
    onClose(applet);
  };
  const minimiseWindow = async () => {
    ref.current?.classList.add("animate__fadeOutDown");
    await wait(260);
    onMinimise(applet);
  };

  return (
    <div
      key={applet.id}
      ref={ref}
      id={`window-${applet.id}`}
      onMouseDown={() => focusWindow(applet.id)}
      style={{ zIndex: 20 + focusIndex }}
      className={classNames(
        "group absolute top-2 left-2 min-w-[250px] min-h-[100px] z-10 border border-slate-500 bg-slate-300 select-none outline-none animate__animated fast animate__fadeIn",
        {
          "shadow-brutal": !applet.fullscreen,
          hidden: applet.minimised,
        }
      )}
    >
      <Resizer
        onResize={(direction, e) =>
          onResize(ref.current!, direction, e, applet)
        }
      />
      <Dragger fullWidth={true} onDrag={(e) => onDrag(ref.current!, e, applet)}>
        <div
          className={classNames("flex justify-between px-1 py-2 text-white", {
            "emboss-out_portfolio": isWindowFocused,
            "emboss-out_inactive": !isWindowFocused,
          })}
        >
          <h2 className="select-none">{applet.title}</h2>
          <div className="flex gap-1">
            <Button
              aria-label={`Minimise ${applet.title} window`}
              onClick={minimiseWindow}
              className={classNames("py-0.5", {
                "emboss-out_portfolio--light hover:bg-portfolio-400 active:emboss-in_portfolio--light":
                  isWindowFocused,
              })}
            >
              <FontAwesomeIcon icon={faMinus} />
            </Button>
            <Button
              aria-label={`Maximise ${applet.title} window`}
              onClick={(e) => {
                ref.current?.focus();
                onMaximise(e, ref.current!, applet);
              }}
              className={classNames("py-0.5", {
                "emboss-out_portfolio--light hover:bg-portfolio-400 active:emboss-in_portfolio--light":
                  isWindowFocused,
              })}
            >
              {applet.fullscreen ? (
                <FontAwesomeIcon icon={faSquareMinus} />
              ) : (
                <FontAwesomeIcon icon={faSquare} />
              )}
            </Button>

            <Button
              aria-label={`Close ${applet.title} window`}
              onClick={closeWindow}
              className={classNames("py-0.5", {
                "emboss-out_portfolio--light hover:bg-portfolio-400 active:emboss-in_portfolio--light":
                  isWindowFocused,
              })}
            >
              <FontAwesomeIcon icon={faClose} />
            </Button>
          </div>
        </div>
      </Dragger>
      <div className="h-[calc(100%-42px)] overflow-auto emboss-out border-t-0">
        {applet.content({})}
      </div>
    </div>
  );
};
